@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;900&display=swap);
body {
	margin: 0;
	font-family: "Poppins", sans-serif;
	color: #3f3f3f;
}

.nav {
	display: flex;
	justify-content: space-between;
	margin-bottom: 2rem;
	padding: 1rem 2rem;
	position: fixed;
	width: -webkit-fill-available;
	width: -moz-available;
	background-color: white;
	top: 0;
	z-index: 5;
}

.nav a {
	text-decoration: none;
	color: #3f3f3f;
}

.nav .logo {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.nav a h1 {
	margin: 0;
	font-weight: 600;
}

.nav ul {
	list-style-type: none;
	margin: 0;
	padding: 1rem 0;
}

.nav ul li {
	display: inline;
	margin-left: 4rem;
	font-size: 1.25rem;
}

.cart-nav {
	display: inline;
	position: relative;
}

.cart-nav span {
	position: absolute;
	bottom: 15px;
	left: 16px;
	background: #f7d372;
	font-size: 1rem;
	height: 24px;
	width: 24px;
	border-radius: 50%;
	text-align: center;
}

.active {
	border-bottom: solid 6px #f05050;
}

.home {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	padding: 2rem;
	margin-top: 8rem;
}

#home-content {
	max-width: 550px;
	width: 50%;
}

#home-content h1 {
	font-weight: 900;
	letter-spacing: 4px;
	max-width: 20ch;
}

.home img {
	width: 40%;
	max-width: 300px;
}

.home a button {
	color: #3f3f3f;
	border: none;
	background-color: #f7d372;
	cursor: pointer;
	padding: 1rem 2rem;
	border-radius: 8px;
	font-family: "Poppins", sans-serif;
	font-weight: 700;
	font-size: 1.05rem;
	box-shadow: 0 3px 3px 0px #0000002e;
}

.home a button:hover {
	background-color: #f7c847;
}

.shop-container {
	display: flex;
	margin-top: 8rem;
}

.side-bar {
	width: 20%;
	position: fixed;
}

.side-bar a {
	text-decoration: none;
	color: #3f3f3f;
	display: block;
	margin: 2rem;
	padding: 0.5rem;
}

.active-side-bar {
	border-left: solid 6px #f05050;
	font-weight: 600;
}

.shop {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
	grid-gap: 3rem;
	grid-gap: 3rem;
	gap: 3rem;
	padding: 1rem;
	justify-items: center;
	width: 80%;
	margin-left: 20%;
}

.item-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	box-shadow: 0 3px 3px 0px #0000002e;
	justify-content: center;
	border-radius: 8px;
	transition: 0.3s;
}

.item-card img {
	width: 12rem;
	border-radius: 8px;
	margin: 1rem;
}

.item-card .item-name {
	font-weight: 600;
}

.item-card .item-name,
.item-card .item-price {
	margin: 0;
	margin-top: 1em;
}

.item-card button {
	margin-top: 1em;
	margin-bottom: 1em;
	color: #3f3f3f;
	background-color: #f7d372;
	border: none;
	box-shadow: 0 3px 3px 0px #0000002e;
	padding: 0.75rem 2.5rem;
	border-radius: 8px;
	cursor: pointer;
	font-size: 1rem;
	font-family: "Poppins", sans-serif;
	font-weight: 700;
}

.item-card button:hover {
	background-color: #f7c847;
}

.cart {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 8rem;
}

.cart .checkout-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 2rem;
	width: 100%;
}

.cart .checkout-container .total-checkout {
	font-size: 1.5rem;
	font-weight: 700;
}

.cart .checkout-container button {
	margin-top: 1em;
	margin-bottom: 1em;
	color: #3f3f3f;
	background-color: #f7d372;
	border: none;
	box-shadow: 0 3px 3px 0px #0000002e;
	padding: 0.75rem 2.5rem;
	border-radius: 8px;
	cursor: pointer;
	font-size: 1rem;
	font-family: "Poppins", sans-serif;
	font-weight: 700;
}

.cart .checkout-container button:hover {
	background-color: #f7c847;
}

.cart .selected-phones-container .empty-cart-msg {
	margin: 0 auto;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.item-in-cart {
	display: flex;
	box-shadow: 0 3px 3px 0px #0000002e;
	border-radius: 8px;
	margin-bottom: 2rem;
}

.item-in-cart .item-image-container img {
	width: 12rem;
	border-radius: 8px;
	margin: 1rem;
}

.item-in-cart .item-info-container {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin: 1rem;
}

.item-in-cart .item-info-container p:first-child {
	font-weight: 600;
	font-size: 1.25rem;
}

.item-in-cart .item-info-container p {
	margin: 0;
}

.item-in-cart .item-info-container label {
	font-weight: 500;
	cursor: pointer;
}

.item-in-cart .item-info-container input {
	margin-left: 0.75rem;
	padding: 0.75rem;
	font-size: 1rem;
	background-color: #ededed;
	border: none;
	border-radius: 6px;
}

.item-in-cart .item-info-container span {
	font-size: 1rem;
	cursor: pointer;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.item-in-cart .item-info-container span:hover {
	color: red;
}

.item-in-cart .item-info-container span .remove-button {
	display: inline;
	margin-left: 0.5rem;
}

.modal {
	display: block;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.25);
	z-index: 10;
}

.modal-content {
	background-color: white;
	padding: 1rem;
	margin: 10rem auto;
	width: 60%;
	max-width: 450px;
	border-radius: 12px;
	text-align: center;
}

.icon {
	font-size: 5rem;
	color: #62c962;
}

.modal-content button {
	color: #3f3f3f;
	background-color: #f7d372;
	border: none;
	box-shadow: 0 3px 3px 0px #0000002e;
	padding: 0.75rem 2.5rem;
	border-radius: 8px;
	cursor: pointer;
	font-size: 1rem;
	font-family: "Poppins", sans-serif;
	font-weight: 700;
}

.modal-content button:hover {
	background-color: #f7c847;
}

